import React from 'react'
import { useApp } from '../../context/Context';
import { useToken } from '../../context/TokenContext';
import textContext from '../../textContext';
import { anchorLogo, mercurialLogo, saberLogo, solanaLogo, terraLogo, terraUST } from '../../assets/images/crypto';

// Hook for checking viewport + animation package
import { useInView } from 'react-hook-inview';
import SmoothList from 'react-smooth-list';

export default function Strategy() {

	const { language, width } = useApp();
	const { anchor } = useToken();
  const text = textContext[language].strategy;

	const [view, setView] = React.useState(false);
  const [ref, isVisible] = useInView({
    threshold: width > 900 ? 0.5 : 0,
  })

  // Trigger setView on viewport enter
  React.useEffect(() => {
  	if (width < 900) {
  		setTimeout(() => {
  			isVisible ? setView(true) : setView(false)
  		}, 500)
  	} else {
  		isVisible ? setView(true) : setView(false)
  	}
  }, [isVisible, width])

  const invest = [
  	{ iconOne: terraLogo, iconTwo: anchorLogo, title: 'Terra/Anchor', apy: anchor.APY },
  	{ iconOne: solanaLogo, iconTwo: saberLogo, title: 'Saber BUSDbs/USDC', apy: 0.15 },
  	{ iconOne: solanaLogo, iconTwo: mercurialLogo, title: 'Mercurial USD-4Pool', apy: 0.1271 },
  ];

  const investPlus = [
  	{ iconOne: terraUST, title: 'Δ-neutral mNFLX', apy: 0.3453 },
  	{ iconOne: terraUST, title: 'Δ-neutral mSQ', apy: 0.3316 },
  	{ iconOne: terraUST, title: 'Δ-neutral mTWTR', apy: 0.3252 },
  	{ iconOne: terraUST, title: 'Δ-neutral mGS', apy: 0.3177 },
  	{ iconOne: terraUST, title: 'Δ-neutral mGOOGL', apy: 0.2983 },
  ];

	return (
		<div id="strategy">
			<div ref={ref} className="padding">
				<p className="textHeader">{text.title}</p>
				<p className="subHeader"><span className="colorPrimary">{text.investPlus.subTitle.split(" ")[0]}</span> <span className="colorSecondary">{text.investPlus.subTitle.split(" ")[1]} {text.investPlus.subTitle.split(" ")[2]}</span></p>
				<SmoothList transitionDuration={1000} visible={view} className="cardContainer">
					{
						investPlus.map((l, i) => (
							<div key={i} className="strategyCard">
								<span>
									<div className="poolImages">
										<img src={l.iconOne} alt={l.pool} />
										{l.iconTwo && <img src={l.iconTwo} alt={l.pool} />}
									</div>
									<p>{l.title}</p>
								</span>
								<span>
									<div className="apy">APY</div>
									<p className="percentClass">{(l.apy*100).toFixed(2)}%</p>
								</span>
							</div>
						))
					}
				</SmoothList>
				<p className="colorPrimary subHeader">{text.invest.subTitle}</p>
				<SmoothList transitionDuration={1000} visible={view} className="cardContainer">
					{
						invest.map((l, i) => (
							<div key={i} className="strategyCard">
								<span>
									<div className="poolImages">
										<img src={l.iconOne} alt={l.pool} />
										<img src={l.iconTwo} alt={l.pool} />
									</div>
									<p>{l.title}</p>
								</span>
								<span>
									<div className="apy">APY</div>
									<p className="percentClass">{(l.apy*100).toFixed(2)}%</p>
								</span>
							</div>
						))
					}
				</SmoothList>
			</div>
		</div>
	)
};